.menu {
  width: 50%;

  position: absolute;
  top: 30%;
  left: 50%;

  -webkit-transform: translate(-50%, -30%);

          transform: translate(-50%, -30%);
}

.menu-btn {
  display: block;

  width: 100%;
  margin-bottom: 1.5vmin;
}

.menu-btn:last-child {
  margin-bottom: 0;
}

/* media */

@media screen and (max-width: 1920px){
  .menu {
    width: 60%;
  }
}

@media screen and (max-width: 1024px){
  .menu {
    width: 70%;
  }
}

@media screen and (max-width:  480px){
  .menu {
    width: 90%;
  }
}


.game {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  height: 80vh;
  width: 100vw;
  padding: 5vmin;
  padding-bottom: 2vmin;

  position: fixed;
  top: 0;
  right: 0;
  left: 0;
}

.bottom-interface {
  position: relative;

  left: 0;
  right: 0;
  z-index: 3;

  display: flex;
  justify-content: space-between;
}

.game-btn {
  min-width: 30%;
}

@media screen and (orientation: portrait) and (max-width: 480px) {
  .game {
    height: 75vh;
    padding-top: 1vmin;

    justify-content: space-evenly;
  }
}
.board {
  width: 100%;
  height: 80%;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
/* item */
.item {
  display: inline-block;
  position: relative;

  padding: 1vmin;
  border-radius: 1vmin;

  text-align: center;
  background-color: rgba(255, 255, 255, 0.1);
}

.item.level-easy {
  width: 24%;
  height: 32%;
}

.item.level-middle, .item.level-hard {
  width: 19%;
  height: 24%;
}

/* img wrapper */
.img-wrapper {
  position: absolute;

  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  width: 100%;
  height: 100%;

  border-radius: 1vmin;
  background-color: #6d85af;
}

/* wrapper for checked items on result step */
.img-wrapper.finish-step {
  opacity: 0.3;
}

.img-wrapper.finish-step.correct {
  background-color: #02fa75;
}

.img-wrapper.finish-step.wrong {
  background-color: #ff0049;
}

/* img */
.img {
  height: 100%;
  width: 100%;
}

/* img-btn */
.img-btn {
  position: absolute;

  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  width: 100%;
  height: 100%;

  border-radius: 1vmin;

  font-size: 1.5em;
}

.img-btn:disabled {
  opacity: 1;
}

.img-btn.disabled-focus:disabled {
  border: solid calc(max(0.5vmin, 2px)) #ffffff;
}

.item.correct .img-btn {
  background-color: #579b77;
}

.item.wrong .img-btn {
  background-color: #b0556f;
}

.item.unmarked .img-btn {
  border: 0px;
  background-color: #61758a;
}

.item.unmarked .img-btn:hover {
  background-color: #56687a;
}


.item.unmarked .img-btn:focus {
  border: 0px;
  background-color: #7566b6;
}

/* media orientation */

@media (orientation: portrait) {
  .item.level-easy {
    width: 32%;
    height: 24%;
  }

  .item.level-middle, .item.level-hard {
    width: 24%;
    height: 19%;
  }
}

@media (orientation: landscape) {
  .item.level-easy {
    width: 24%;
    height: 32%;
  }

  .item.level-middle, .item.level-hard {
    width: 19%;
    height: 24%;
  }
}

/* media width */
@media screen and (max-width: 3840px){
  .img-btn {
    font-size: 1.3em;
  }
}

@media screen and (max-width: 1920px){
  .img-btn {
    font-size: 1.1em;
  }
}

@media screen and (max-width: 1024px){
  .img-btn {
    font-size: 1em;
  }
}

/* wrapper */

.wrapper {
  position: absolute;
  z-index: 1;

  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;

  background-color: rgba(000, 000, 000, 0.4);
}

.wrapper.invisible {
  display: none;
}

/* iframe */
iframe {
  height: 25vh;
}

/* content */
.content {
  display: inline-block;

  position: relative;
  top: 30%;
  left: 50%;
  -webkit-transform: translate(-50%, -30%);
          transform: translate(-50%, -30%);

  font-size: 2.5em;
  font-weight: bold;
  color: #fff;
}

.content .countdown {
  font-size: 3em;
}

.content .result-label, .result {
  display: block;

  margin-bottom: 1vmin;

  text-align: center;
  text-transform: uppercase;
}

.content .result {
  font-size: 1.2em;

  color: #8bffbf;
}

/* media */

@media screen and (max-width: 5120px){
  .content {
    font-size: 2em;
  }
}

@media screen and (max-width: 1920px){
  .content {
    font-size: 1.4em;
  }
}

@media screen and (max-width: 1024px){
  .content {
    font-size: 1.2em;
  }
}

@media screen and (max-width:  768px){
  .content {
    font-size: 1em;
  }
}

@media screen and (orientation: portrait) {
  .content {
    font-size: 1.8em;
  }
}

